body {
  background-color: black;
  color: white;
  font-family: "Inter", sans-serif;
  overflow-x: hidden;
  padding: 0;
  margin: 0;
}

.wrapper {
  position: relative;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.intro {
  position: absolute;
  margin: auto;
  width: 25vw;
}

.intro.active {
  animation: enter 0.5s forwards ease-out;
}

.intro.hidden {
  animation: exit 0.5s forwards ease-out;
}

@keyframes enter {
  0% {
    left: 0;
    top: 50%;
    transform: translate(-200%, -50%);
  }
  100% {
    left: 42.5%;
    top: 50%;
    transform: translate(-100%, -50%);
  }
}

@keyframes exit {
  0% {
    left: 42.5%;
    top: 50%;
    transform: translate(-100%, -50%);
  }
  100% {
    left: 0;
    top: 50%;
    transform: translate(-200%, -50%);
  }
}

#hi {
  font-size: 5rem;
  margin: 0.1em 0em;
  padding: 0;
}

#myName {
  font-size: 2rem;
  margin: 0.1em 0em;
  padding: 0;
  min-width: 25vw;
}

#NYU:hover {
  color: #8900e1;
}

#myName u {
  color: #f9c74f;
}

#bio {
  font-size: 0.85rem;
  margin: 0.5em 0em;
  padding: 0;
  background-color: rgba(0, 0, 0, 0.46);
}

.orbitSystem {
  position: absolute;
  top: 50%;
  left: 57%;
  transform: translate(-50%, -50%);
  -webkit-transition: left 0.5s ease-out; /* Chrome 1-25, Safari 3.2+ */
  -moz-transition: left 0.5s ease-out; /* Firefox 4-15 */
  -o-transition: left 0.5s ease-out; /* Opera 10.50â€“12.00 */
  transition: left 0.5s ease-out;
}
.leftOrbit {
  left: 0;
  transform: translate(-50%, -50%);
}

.orbiters {
  position: absolute;
  width: 676px;
  height: 676px;
  top: 49.55%;
  transform: translate(0, -50%);
  display: block;
}

.orbiters2 {
  width: 676px;
  height: 676px;
  animation: spin-right 18s linear infinite;
  top: 100%;
}

.movingLine {
  position: absolute;
  left: 676px;
  top: 48.95%;
  transform: translate(-100%, -50%);
  opacity: 0.55;
}
#p1 {
  position: absolute;
  left: 45em;
  top: -1em;
  transform: translate(-50%, -50%);
}
#p2 {
  position: absolute;
  left: 2em;
  top: 2em;
  transform: translate(-50%, -50%);
}
#p3 {
  position: absolute;
  top: 45em;
  left: 10em;
  transform: translate(-50%, -50%);
}

.panel {
  animation: slide-away 0.5 forwards ease-out;
  position: absolute;
  width: 60vw;
  height: 85vh;
}

.clipList {
  //right: 7vw;
  .pubRow {
    display: flex;
    height: fit-content;
    margin-bottom: 1em;

    margin: 0.5em;
    .title {
      margin: 0;
      font-size: 2rem;
      width: 70%;
      height: 100%;
      padding: 1em;
      text-decoration: underline;
    }
    .articles {
      width: 100%;
      margin-top: 0.5em;
      font-size: 1.25rem;
      line-height: 1.8rem;
    }
  }
  overflow-y: scroll;
}
.active {
  animation: slide-into 0.5s forwards ease-out;
  top: 12vh;
  left: 100%;
}

.panel.hidden {
  top: 12vh;
  right: -100%;
  animation: slide-away 0.5s forwards ease-out;
}

@keyframes slide-away {
  0% {
    transform: translate(50%, 0%);
  }
  100% {
    transform: translate(170%, 0%);
    color: black;
  }
}

@keyframes slide-into {
  0% {
    transform: translate(0%, 0);
    color: black;
  }
  100% {
    transform: translate(-110%, 0%);
  }
}

.clipList::-webkit-scrollbar {
  display: none;
}

.credit {
  position: fixed;
  bottom: 0;
  left: 0;
  font-size: small;
  margin-left: 1em;
  background-color: rgba(0, 0, 0, 0.318);
  a {
    color: #f8c650;
  }
}

a {
  color: #f8c650;
}

::-webkit-scrollbar {
  display: none;
}
div.info.vertical {
  position: inherit;
  display: block;
  min-width: 70%;
}
.item.vertical {
  display: flex;
  width: 100%;
  height: fit-content;
  .skills.vertical {
    display: flex;
    position: absolute;
    margin-left: 1em;
    margin-top: 0;
  }
}

p.about.vertical {
  top: 2em;
  display: block;
  position: absolute;
  margin-top: 1em;
  margin-left: 1em;
}

.dataPanel {
  overflow-y: scroll;
  overflow-x: hidden;
  ::-webkit-scrollbar {
    display: none;
  }
  a {
    font-size: 1.3rem;
  }

  .item {
    margin-top: 2.5em;
    position: relative;
    .frame {
      ::-webkit-scrollbar {
        display: none;
      }
    }
    iframe {
      background-color: lightgray;
      height: 450px;
      width: 300px;
      padding-top: 1em;
      padding-left: 1em;
    }
    .skills {
      width: fit-content;
      display: flex;
      gap: 0.75em;
      padding: 0;
      margin: 0.75em 0;
      .tech {
        margin-top: 0.5em;
        float: left;
        width: fit-content;
        max-width: 100%;
        font-size: smaller;
        list-style: none;
        text-align: center;
        padding: 0.5em;
        border-radius: 6px;
        p {
          max-width: 100%;
          padding: 0;
          margin: 0;
        }
      }
    }

    p.about {
      max-width: 45vw;
      background-color: rgba(0, 0, 0, 0.396);
    }
  }
}

.dataPanel.hidden {
}
.frame {
}

.menu {
  position: absolute;
  width: 8em;
  list-style: none;
  padding: 0;
  .menuItem {
    button {
      width: 100%;
      margin: 1em;
      font-size: larger;
      text-decoration: underline;
      background: none;
      border: none;
    }
    button.notSelected {
      color: #f8c650;
      &:hover {
        color: white;
      }
    }
  }
}

.menu.regular {
  animation: go-back 0.5s forwards ease-out;
  text-align: right;
  button {
    text-align: right;
  }
}

.leftMenu {
  text-align: left;
  animation: slide-out 0.5s forwards ease-out;
  button {
    text-align: left;
  }
}

@keyframes go-back {
  0% {
    left: 8em;
    top: 50%;
    transform: translate(50%, -50%);
  }
  100% {
    left: 100%;
    top: 50%;
    transform: translate(-125%, -50%);
  }
}

@keyframes slide-out {
  0% {
    left: 100%;
    top: 50%;
    transform: translate(-125%, -50%);
  }
  100% {
    left: 8em;
    top: 50%;
    transform: translate(50%, -50%);
  }
}

@keyframes spin-right {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.resume {
  overflow-y: scroll;
}

section.resume {
  height: -moz-fit-content;
  height: fit-content;
}

.resume-section {
  display: grid;
  grid-template-columns: repeat(2, auto);
  text-align: left;
  h3 {
    margin-top: 0.5em;
    width: 10vw;
    text-decoration: underline;
    font-size: x-large;
  }
  h4 {
    font-size: larger;
    text-decoration: underline;
    margin-bottom: 0.5em;
    color: #f8c650;
  }
  em {
    margin-top: 0.5em;
    padding: 0;
    margin-bottom: 0em;
    color: gray;
  }
  .details {
    font-size: large;
    width: 35vw;
  }
  .details ul {
    padding-top: 0;
    margin-top: 0.5em;
  }
  .details ul li {
    padding-top: 0em;
    margin-top: 0.75em;
  }
}

button.selected {
  color: #d59442;
  text-emphasis: none;
  &:hover {
    color: white;
  }
}

@media only screen and (max-device-width: 950px) {
  .dataPanel {
    position: absolute;
    padding-left: 1em;
    width: 50%;
    margin-right: 2em;
    height: 75%;
  }

  .item.vertical {
    display: block;
    p {
    }
    .about.vertical {
      width: 100%;
    }
  }

  .info.vertical {
    height: 100%;
    width: 100%;
  }
}
